@keyframes typewriter {
    from { width: 0; }
    to { width: 100%; }
  }
  
  @keyframes blinkTextCursor {
    from { border-right-color: rgba(255, 255, 255, 0); }
    to { border-right-color: rgba(255, 255, 255, 1); }
  }
  
  /* HomePage.css */
  @keyframes blinkCursor {
    from { border-right-color: transparent; }
    to { border-right-color: rgba(0, 0, 0, 1); }
  }
  
  .typewriter {
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    border-right: 2px solid currentColor;
    animation: typewriter 4s steps(40, end) 1s 1 normal both, 
               blinkCursor 500ms steps(40, end) infinite;
  }
  
  
  /* To add delete effect */
  @keyframes typewriterDelete {
    from { width: 100%; }
    to { width: 0; }
  }
  
  .typewriter-delete {
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    border-right: 2px solid white;
    animation: typewriter 4s steps(40, end) 1s 1 normal both, 
               blinkCursor 500ms step-end infinite,
               typewriterDelete 4s steps(40, end) 5s 1 normal both;
  }
  
  .clip-path-diagonal {
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
  }
  